@charset "utf-8";

@import "../common/base";
@import "../common/variables";
@import "../common/mixins";

/* For screen width from 1200px to 1366px */
@media screen and (min-width: 768px) and (max-width: 1024px) and (orientation:portrait){
    .account-container{
        width: $accountContainer - 100;
    }
    .dgt-table {
        .tbody {
            font-size: 14px;
            .total{
                font-size: 13px;
            }
        }
    }
    #soffos-checkout {
        form, #checkout-button{
            flex: 0 0 33.33333%;
        }
    }
}

