/**
 * Base Rules
 */
@import "variables";
@import "mixins";

html, body {
    height: 100vh;
}

body {
    background-color: $whiteColor;
}

*{
    outline: none;
}

/* General Class */
a {
    color: $link;
    &:hover {
        color: $linkHover;
        text-decoration: none;
    }
    &:focus{
        color: $linkHover;
        text-decoration: none;
    }
}

label {
    font-weight: normal;
    margin: 0;
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.dgt-center {
    text-align: center;
}

.dgt-left {
    text-align: left;
}

.dgt-right {
    text-align: right;
}

.dgt-pl0 {
    padding-left: 0;
}

.dgt-pr0 {
    padding-right: 0 !important;
}

.dgt-mt0 {
    margin-top: 0 !important;
}

.dgt-mb0 {
    margin-bottom: 0px !important;
}

.dgt-mr0 {
    margin-right: 0 !important;
}

.dgt-ml0 {
    margin-left: 0 !important;
}

.dgt-pb0 {
    padding-bottom: 0 !important;
}

/* Transition General */
a {
    @include css3-compatible("transition", all 0.3s linear 0s);
}
#form-chat-message{
    @include css3-compatible("transition", all 0.2s linear 0s);
}

/* Button general */
.dgt-button {
    line-height: $baseLineHeight + 16;
    padding: 0 21px;
    display: block;
    color: #fff;
    background-color: $accent;
    text-align: center;
    @include border-radius(20px, 20px, 20px, 20px);
    font-size: $baseFontSize + 2;
    border: none;
    margin: 0;
    position: relative;
    outline: none;
    &:hover, &:focus {
        background-color: $accent;
        color: $whiteColor;
        text-decoration: none;
    }
    &:disabled{
        @include opacity(0.5);
    }
    i{
        font-size: $baseFontSize + 22;
        margin-right: 15px;
    }
}
.dgt-button-large{
    font-size: $baseFontSize + 6;
    line-height: $baseLineHeight + 32;
    text-transform: uppercase;
    font-weight: bold;
    width: 100%;
    @include border-radius(30px, 30px, 30px, 30px);
}
.dgt-button-medium{
    font-size: $baseFontSize + 6;
    line-height: $baseLineHeight + 24;
    width: 100%;
    @include border-radius(24px, 24px, 24px, 24px);
}
.dgt-button-gray{
    background-color: $grayColor;
}

/* Title */
.dgt-title {
    font-size: $baseFontSize + 18;
    line-height: $baseLineHeight + 18;
    color: $titleColor;
    margin: 0 0 10px;
    .dgt-icon-custom{
        vertical-align: 4px;
    }
}